@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  background-color: #f8fdff;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #f2f5f7;
}
.wrapper .main-header,
.wrapper .main-footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.main-header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 30px 0 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 5;
}

.main-header__container {
  max-width: 1300px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.main-header__logo {
  margin-right: 30px;
}

.main-header__logo img {
  min-width: 215px;
}

.main-header__logo-link:hover,
.main-header__logo-link:focus {
  opacity: 0.8;
}

.main-header__logo-link:active {
  opacity: 0.6;
}

.main-header__contacts {
  margin-right: 33px;
  padding: 15px 0 20px 20px;
}

.main-header__button {
  max-width: 260px;
  margin-right: 60px;
}

@media (max-width: 1023px) {
  .main-header {
    position: absolute;
    padding: 20px 0 25px;
  }
  .main-header__container {
    padding: 0 50px;
  }
  .main-header__contacts {
    margin-right: 20px;
    padding: 0 0 15px;
  }
  .main-header__button {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding: 15px 0 20px;
  }
  .main-header__container {
    padding: 0 25px;
    align-items: flex-start;
  }
  .main-header__logo {
    margin-left: 1px;
  }
  .main-header__logo img {
    min-width: 35px;
  }
  .main-header__contacts {
    margin-right: 10px;
    padding: 0;
  }
}
.contacts {
  display: flex;
  flex-wrap: wrap;
  gap: 27px;
  min-width: 500px;
}

.contacts__email {
  display: flex;
  gap: 14px;
  max-width: 225px;
  padding-right: 95px;
}
.contacts__email:hover, .contacts__email:focus {
  opacity: 0.8;
}
.contacts__email:active {
  opacity: 0.6;
}

.contacts__email-icon {
  fill: #00b2ff;
}

.contacts__text {
  color: #f2f5f7;
}

.contacts__phone {
  display: flex;
  gap: 15px;
  max-width: 225px;
}
.contacts__phone:hover, .contacts__phone:focus {
  opacity: 0.8;
}
.contacts__phone:active {
  opacity: 0.6;
}

.contacts__phone-icon {
  fill: #00b2ff;
}

@media (max-width: 1023px) {
  .contacts {
    gap: 20px;
    min-width: 100px;
  }
  .contacts__email {
    padding: 10px;
  }
  .contacts__phone {
    padding: 10px;
  }
  .contacts__text {
    display: none;
  }
}
@media (max-width: 767px) {
  .contacts__email {
    padding: 5px 11px 10px 9px;
  }
  .contacts__phone {
    padding: 5px 10px 10px;
  }
}
.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #f2f5f7;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.05em;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}
.button--light {
  min-width: 161px;
  margin-top: 5px;
  padding: 10px 16px 12px 17px;
  background-color: transparent;
  border: 1px solid #f2f5f7;
}
.button--promo {
  display: inline-block;
  max-width: 411px;
  margin-left: 1px;
  padding: 15px 28px 19px 26px;
  font-size: 18px;
  line-height: 21px;
  background-color: #00b2ff;
}
.button--promo::after {
  content: attr(data-button-promo-desktop);
}
.button--contrast {
  min-width: 180px;
  margin-left: 1px;
  padding: 13px 13px 12px 12px;
  color: #00b2ff;
  background-color: transparent;
  border: 1px solid #00b2ff;
}
.button--dark {
  min-width: 265px;
  padding: 15px 25px 10px;
  background-color: #041827;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  color: #ffffff;
}
.button--modal {
  min-width: 100%;
  padding: 16px 25px 10px;
  background-color: #041827;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  color: #ffffff;
}

.button--light:hover,
.button--light:focus {
  color: #00b2ff;
  border-color: #00b2ff;
  opacity: 0.8;
}

.button--light:active {
  color: #00b2ff;
  border-color: #00b2ff;
  opacity: 0.6;
}

.button--promo:hover,
.button--promo:focus {
  background-color: #f2f5f7;
  color: #00b2ff;
}

.button--promo:active {
  background-color: #f2f5f7;
  color: #00b2ff;
  opacity: 0.8;
}

.button--contrast:hover,
.button--contrast:focus {
  background-color: #00b2ff;
  color: #f2f5f7;
}

.button--contrast:active {
  background-color: #00b2ff;
  color: #f2f5f7;
  opacity: 0.6;
}

.button--dark:hover,
.button--dark:focus {
  background-color: #f2f5f7;
  color: #041827;
}

.button--dark:active {
  background-color: #f2f5f7;
  color: #041827;
  opacity: 0.8;
}

.button--modal:hover,
.button--modal:focus {
  background-color: #f2f5f7;
  color: #041827;
}

.button--modal:active {
  background-color: #f2f5f7;
  color: #041827;
  opacity: 0.8;
}

@media (max-width: 1023px) {
  .button--promo {
    margin-left: 45px;
    padding: 16px 28px 18px 26px;
  }
  .button--contrast {
    margin-left: 0;
    padding: 14px 13px 12px 11px;
  }
  .button--dark {
    padding: 17px 25px 9px;
  }
}
@media (max-width: 767px) {
  .button--promo {
    display: block;
    margin-left: 0;
    padding: 13px 30px 11px;
    font-size: 14px;
    line-height: 16px;
  }
  .button--promo::after {
    content: attr(data-button-promo-mobile);
  }
  .button--contrast {
    min-width: 160px;
    padding: 8px 10px 9px;
  }
  .button--dark {
    min-width: 270px;
    padding: 16px 25px 10px;
  }
}
.promo {
  position: relative;
  width: 100%;
  min-height: 880px;
  margin-bottom: 120px;
  padding: 305px 0 280px;
  background-color: #041827;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;
}
@supports not (background-image: url("../img/promo-bg.webp")) {
  .promo {
    background-image: url("../img/${bg}.@{ext}");
  }
}
@supports (background-image: url("../img/promo-bg.webp")) {
  .promo {
    background-image: url("../img/promo-bg.webp");
  }
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  @supports not (background-image: url("../img/promo-bg@2x.webp")) {
    .promo {
      background-image: url("../img/promo-bg@2x.@{ext}");
    }
  }
  @supports (background-image: url("../img/promo-bg@2x.webp")) {
    .promo {
      background-image: url("../img/promo-bg@2x.webp");
    }
  }
}

.promo__container {
  max-width: 1300px;
  padding-left: 165px;
}

.promo__container h2 {
  margin: 0;
  margin-bottom: 32px;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f7;
}

.promo__container p {
  max-width: 685px;
  margin: 0;
  margin-bottom: 46px;
  padding: 0;
  font-size: 22px;
  line-height: 34px;
}

@media (max-width: 1023px) {
  .promo {
    min-height: 640px;
    margin-bottom: 80px;
    padding: 179px 0 203px;
    background-position: 20% 0;
  }
  .promo__container {
    padding: 0 50px;
  }
  .promo__container h2 {
    margin-bottom: 2px;
    font-size: 64px;
    line-height: 75px;
  }
  .promo__container p {
    margin-bottom: 56px;
  }
}
@media (max-width: 767px) {
  .promo {
    top: -2px;
    min-height: 560px;
    margin-bottom: 47px;
    padding: 147px 0 190px;
    background-position: 25.5% 0;
  }
  .promo__container {
    padding: 0 25px;
  }
  .promo__container h2 {
    margin-top: 4px;
    margin-bottom: 7px;
    font-size: 36px;
    line-height: 43px;
  }
  .promo__container p {
    max-width: 400px;
    margin-bottom: 44px;
    margin-left: -1px;
    font-size: 14px;
    line-height: 22px;
  }
}
.benefits {
  margin-bottom: 120px;
}

.benefits__container {
  max-width: 1300px;
}

.benefits__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.benefits__item {
  min-height: 300px;
  padding: 40px 45px 35px 40px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
}
.benefits__item--price {
  margin-left: 2px;
}

.benefits__item:hover {
  background-color: #00b2ff;
}
.benefits__item:hover h3 {
  color: #f2f5f7;
}
.benefits__item:hover p {
  color: #f2f5f7;
}

.benefits__item:hover .benefits__item-icon {
  fill: #f2f5f7;
}

.benefits__item-icon {
  width: 35px;
  height: 35px;
  margin-bottom: 17px;
  fill: #00b2ff;
}
.benefits__item-icon--suppliers {
  margin-left: 2px;
}
.benefits__item-icon--confidentiality {
  margin-left: -5px;
}

.benefits__item h3 {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
  color: #00b2ff;
}

.benefits__item p {
  margin: 0;
  padding: 0;
  line-height: 22px;
  color: #041827;
}

@media (max-width: 1023px) {
  .benefits {
    margin-bottom: 80px;
  }
  .benefits__container {
    padding: 0 50px;
  }
  .benefits__item {
    min-height: 320px;
    padding: 40px 30px 28px 40px;
  }
  .benefits__item--suppliers {
    margin-left: -1px;
  }
  .benefits__item--confidentiality {
    min-height: 361px;
  }
  .benefits__item--price {
    min-height: 361px;
    margin-left: 0;
  }
  .benefits__item-icon {
    margin-bottom: 12px;
  }
  .benefits__item-icon--suppliers {
    margin-left: 0;
  }
  .benefits__item-icon--confidentiality {
    margin-top: -1px;
    margin-bottom: 10px;
    margin-left: 0;
  }
  .benefits__item-icon--price {
    margin-bottom: 6px;
    margin-left: 1px;
  }
  .benefits__item h3 {
    margin-bottom: 7px;
  }
  .benefits__item--suppliers p {
    margin-left: 1px;
  }
  .benefits__item--price h3 {
    margin-bottom: 3px;
