  }
  .address.is-active .address__toggle::after {
    opacity: 0;
  }
  .address__item {
    margin-bottom: 11px;
  }
  .address__item--location {
    margin-bottom: 18px;
  }
  .address__item:last-child {
    margin-bottom: 15px;
  }
  .address__link {
    font-size: 14px;
  }
}
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 38px 40px 40px 40px;
  background-color: #00b2ff;
  border-radius: 3px;
  transform: translateX(-8px) translateY(-14px);
}
.modal__close-button {
  position: absolute;
  top: 18px;
  right: 14px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__content h2 {
  margin: 0;
  margin-bottom: 3px;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 42px;
  color: #ffffff;
}

.modal__content p {
  max-width: 380px;
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
}

.modal__content form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 420px;
  padding-top: 40px;
  padding-bottom: 4px;
  font-family: "PT Mono", "Arial", sans-serif;
  line-height: 22px;
}

.modal__input--name {
  order: 1;
}
.modal__input--phone {
  order: 2;
}
.modal__input--question {
  order: 3;
}
.modal__input--checkbox {
  order: 5;
}
.modal__input--button {
  order: 4;
}

.modal__input input[type=text],
.modal__input input[type=tel] {
  width: 100%;
  min-width: 420px;
  padding: 15px;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
  border: none;
  border-radius: 3px;
}
.modal__input input[type=text]::placeholder,
.modal__input input[type=tel]::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
}
.modal__input input[type=text]:hover, .modal__input input[type=text]:focus,
.modal__input input[type=tel]:hover,
.modal__input input[type=tel]:focus {
  outline: 2px solid #f2f5f7;
}
.modal__input input[type=text]:active, .modal__input input[type=text]:focus-within,
.modal__input input[type=tel]:active,
.modal__input input[type=tel]:focus-within {
  outline: 2px solid #041827;
}
.modal__input input[type=text]:disabled,
.modal__input input[type=tel]:disabled {
  background-color: #c5c5c5;
  opacity: 0.6;
}

.modal__input textarea {
  width: 100%;
  min-height: 155px;
  padding: 25px 16px;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
  border: none;
  border-radius: 3px;
}
.modal__input textarea::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
}
.modal__input textarea:hover, .modal__input textarea:focus {
  outline: 2px solid #f2f5f7;
}
.modal__input textarea:active, .modal__input textarea:focus-within {
  outline: 2px solid #041827;
}
.modal__input textarea:disabled {
  background-color: #c5c5c5;
  opacity: 0.6;
}

.modal__input .is-invalid {
  color: #ff1553;
}

.modal__button {
  margin-top: 0;
}

.modal__input--checkbox label {
  display: grid;
  grid-template-columns: 15px 1fr;
  align-items: center;
  gap: 11px;
  margin-left: 9px;
}

.modal__input--checkbox .is-invalid {
  grid-column: 1/-1;
  opacity: 1;
}

.modal__input--checkbox input {
  position: relative;
  display: block;
  width: 15px;
  height: 14px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  appearance: none;
}
.modal__input--checkbox input:checked::after {
  content: "";
  position: absolute;
  top: 0;
  left: 3.5px;
  width: 5px;
  height: 7px;
  border: solid #ffffff;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}
.modal__input--checkbox input:hover, .modal__input--checkbox input:focus {
  outline: 0;
  border-color: #041827;
}
.modal__input--checkbox input:disabled {
  background-color: #c5c5c5;
  border-color: #c5c5c5;
  opacity: 0.8;
}

.modal__input--checkbox span {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
}

